button{
  border: none;
  display: block;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  outline: none;
  overflow: hidden;
  position: relative;
  color: rgb(0, 255, 191);
  font-weight: 700;
  font-size: 19px;
  background-color: #222;
  padding: 17px 60px;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0,0,0,0.20);
  border-radius:5mm;
}

button span {
  position: relative; 
  z-index: 1;
}

button:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 490%;
  width: 140%;
  background: rgb(0,212,255);
  background: linear-gradient(90deg, rgba(0,212,255,1) 50%, rgba(9,9,121,1) 100%);
  transition: all .5s ease-in-out;
  -webkit-transform: translateX(-85%) translateY(-25%) rotate(45deg);
  transform: translateX(-85%) translateY(-25%) rotate(45deg); 
  
}

button:hover:after {
  -webkit-transform: translateX(-15%) translateY(-25%) rotate(45deg);
  transform: translateX(-15%) translateY(-25%) rotate(45deg);
  
}

